body {
  margin: 0;
  padding: 0;
  background-color: #37322d;
  font-family: 'Seravek', -apple-system, BlinkMacSystemFont, 'Helvetica Neue', 'Helvetica', 'Source Han Sans', sans-serif;
  font-size: 1em;
  font-weight: 400;
  color: #c3c8c8;
}
h1,
h2,
h3 {
  font-size: 0.875em;
  font-weight: 400;
  color: #e1dcd7;
}
h1 {
  margin: 8px;
  text-align: center;
}
p {
  font-size: 0.875em;
  font-weight: 400;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  font-size: 0.875em;
  cursor: pointer;
}
.navigator {
  position: fixed;
  width: 100vw;
  height: 28px;
  bottom: 0;
  left: 0;
  background-color: rgba(50, 50, 45, 0.5);
}
.navigator .navigator_content_wrapper {
  height: inherit;
  width: inherit;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.navigator .navigator_content_wrapper .navs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  height: 24px;
  max-width: 50%;
  overflow: hidden;
  margin: 2px;
  cursor: pointer;
}
.navigator .navigator_content_wrapper .tools {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 24px;
  width: 50%;
  margin: 2px;
}
.navigator .navigator_content_wrapper .tools .navigator_button {
  height: 24px;
  line-height: 24px;
  border-radius: 12px;
  margin: 0 8px;
  font-size: 0.875em;
  background-color: #283273;
  padding: 0 4px;
  cursor: pointer;
  color: #e1dcd7;
}
.navigator .navigator_content_wrapper .icon {
  cursor: pointer;
}
.navigator .navigator_content_wrapper .disabled {
  opacity: 0.125;
  cursor: not-allowed;
}
.navigator .navigator_content_wrapper .gallery-name {
  height: 24px;
  line-height: 24px;
  font-size: 0.875em;
  font-weight: 600;
}
.gallery-list li {
  margin: 8px;
}
.gallery-list li .title {
  color: #e1dcd7;
  text-align: center;
  height: 20px;
  line-height: 20px;
}
.photo {
  max-width: 100%;
  display: block;
  position: relative;
  outline: 1px solid #e1dcd7;
  outline-offset: -0.5px;
}
.photo img {
  display: block;
  width: 100%;
}
.caught {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.single-photo {
  text-align: center;
  margin: 8px 4px 32px 4px;
  position: relative;
}
.single-photo .photo_wrapper {
  position: relative;
}
.single-photo .photo_wrapper .photo {
  margin: 0 auto;
  -webkit-box-shadow: 0 32px 64px rgba(50, 50, 45, 0.5);
          box-shadow: 0 32px 64px rgba(50, 50, 45, 0.5);
}
.single-photo .photo_wrapper p {
  margin: 8px 0;
}
.single-gallery {
  text-align: center;
  margin: 8px 4px 32px 4px;
}
.single-gallery li {
  position: relative;
}
.single-gallery li .photo {
  margin: 8px auto;
  -webkit-box-shadow: 0 4px 8px rgba(50, 50, 45, 0.5);
          box-shadow: 0 4px 8px rgba(50, 50, 45, 0.5);
}
