  background-size: 21px 21px;
  background-repeat: no-repeat;
}
.main-logo {
  background-size: 42px 42px;
  background-position: center center;
  width: 44px;
  height: 44px;
  margin: 8px auto 0 auto;
}
.stack {
  position: relative;
  height: 200px;
  width: 200px;
  margin: 8px auto;
}
.stack .anton,
.stack .berta,
.stack .casar {
  display: block;
  outline: 2px solid #e1dcd7;
  outline-offset: -1px;
  -webkit-box-shadow: 0 4px 8px rgba(5, 5, 0, 0.25);
          box-shadow: 0 4px 8px rgba(5, 5, 0, 0.25);
  height: 160px;
  width: 160px;
  position: absolute;
  top: 20px;
  left: 20px;
  -o-object-fit: cover;
     object-fit: cover;
}
.stack .berta {
  -webkit-transform: rotate(-6deg) translateZ(0);
          transform: rotate(-6deg) translateZ(0);
}
.stack .berta {
  -webkit-transform: rotate(-6deg) translateZ(0);
          transform: rotate(-6deg) translateZ(0);
}
.stack .casar {
